const HeaderCarousel = (props) => {
	const [activeIndex, setActiveIndex] = useState(0);
	const [animating, setAnimating] = useState(false);

	const next = () => {
		if (animating) return;
		const nextIndex = activeIndex === photographs.length - 1 ? 0 : activeIndex + 1;
		setActiveIndex(nextIndex);
	}

	const previous = () => {
		if (animating) return;
		const nextIndex = activeIndex === 0 ? photographs.length - 1 : activeIndex - 1;
		setActiveIndex(nextIndex);
	}

	const goToIndex = (newIndex) => {
		if (animating) return;
		setActiveIndex(newIndex);
	}

	const slides = photographs.map((item) => {
		return (
			<CarouselItem
				onExiting={() => setAnimating(true)}
				onExited={() => setAnimating(false)}
				key={item.img}
			>
				<img src={item.img} alt={item.altText} />
				<CarouselCaption captionText={item.caption} captionHeader={item.caption} />
			</CarouselItem>
		);
	});

	return (
		<Carousel
			activeIndex={activeIndex}
			next={next}
			previous={previous}
		>
			<CarouselIndicators items={photographs} activeIndex={activeIndex} onClickHandler={goToIndex} />
			{slides}
			<CarouselControl direction="prev" directionText="Previous" onClickHandler={previous} />
			<CarouselControl direction="next" directionText="Next" onClickHandler={next} />
		</Carousel>
	);
}


class Header extends Component {
	constructor(props) {
		super(props);

		this.toggleNav = this.toggleNav.bind(this);

		this.state = {
			isNavOpen: false
		};
	}

	toggleNav() {
		this.setState({
			isNavOpen: !this.state.isNavOpen
		});
	}



	render() {
		return (
			
			<div className="justify-content border-bottom pb-3 mb-0">
				<HeaderCarousel />
				<Navbar light sticky='top' expand='md'>
					<NavbarToggler onClick={this.toggleNav} />
					<Collapse isOpen={this.state.isNavOpen} navbar>
						<Nav navbar>
							<NavItem>
								<NavLink className='nav-link' to='/home'>Home</NavLink>
							</NavItem>
							<NavItem>
								<NavLink className='nav-link ' to='/engagement'>
									Engagement
											</NavLink>

							</NavItem>
							<NavItem>

								<NavLink className='nav-link' to='/wedding'>
									Wedding
										</NavLink>

							</NavItem>
							<NavItem>

								<NavLink className='nav-link' style={{marginRight:10}} to='/boudoir'>
									Boudoir
										</NavLink>

							</NavItem>
							<NavItem>
								<NavLink className='nav-link' to='/contact'> Contact </NavLink>
							</NavItem>
							<NavItem>
								<NavLink className='nav-link' to={{pathname: 'https://amazon.com'}} href='www.amazon.com'>Shop</NavLink>
							</NavItem>

						</Nav>

					</Collapse>


				</Navbar>
			</div>
		)
	}
}

export default Header;